.chat{
   display: flex;
   padding: 10px;
   cursor: pointer;
   border-bottom: 1px solid whitesmoke;
}
.chat:hover{
   background-color:whitesmoke;
}
.chatInfo >h2{
   font-size: 16px;
   margin-bottom: 6px;
}
.chatInfo{
   margin-left: 12px;
   overflow: visible;
}
a{
   text-decoration: none !important;
   color:black;
}
@media (max-width:500px){
   .chat{
      display: flex;
      padding: 6px;
      cursor: pointer;
      margin-right: 0;
      padding-right: 0;
      border-bottom: 1px solid whitesmoke;
   }
   .chat> h2{
      font-size: 14px;
      margin-bottom: 3px;
      margin-right: 0;
      padding-right: 0;
   }
   .chatInfo >h2{
      font-size: 14px;
      margin-bottom: 4px;
   }
   .chatInfo{
      margin-left: 12px;
      overflow: visible;
      margin-right: 0;
      padding-right: 0;
   }
}