.Left{
   flex: 0.35;
   display: flex;
   flex-direction: column;
}
.left_header{
   display: flex;
   justify-content:space-between;
   padding: 20px;
   margin-right: 0;
}
.left_header_icons{
   display: flex;
   justify-content:flex-start;
   min-width:10vw;
}
.left_search{
   display: flex;
   align-items: center;
   height: 40px;
   padding: 10px;
   background-color: #f6f6f6;
}
.searchContainer{
   display:flex;
   align-items: center;
   width: 100%;
   height: 30px;
   background-color: white;
   border-radius: 20px;
}
.searchContainer >input{
   border: none;
   outline: none;
   margin-left: 10px;
}
.searchContainer > .MuiSvgIcon-root{
   color: grey;
   margin-left: 5px;
}
.Button > button {
   display: none;
}
.left_chats{
   background-color: #dcf8c6;
   flex: 1;
  overflow: scroll;
  overflow-x: hidden;
}
@media (max-width:500px){
   .left_header_icons{
      display: flex;
      flex-direction: row;
      margin: 0;
      padding: 0;
      justify-content: space-around;
   }
   .left_header{
      display: flex;
      justify-content:center;
      flex-direction: column;
      margin: 0;
      padding: 0;
   }
   .pic{
      display: flex;
      justify-content: space-around;
   }
   .Left{
      display: flex;
      flex-direction: column;
      
   }
   .searchContainer{
      display:flex;
      align-items: center;
      height: 20px;
      background-color: whitesmoke;
   }
   .searchContainer >input{
      display: none;
   }
   .searchContainer > .MuiSvgIcon-root{
      color: grey;
      margin: 0 auto;
   }
   .left_chats{
      background-color: #dcf8c6;
     overflow: scroll;
     overflow-x: hidden;
     margin-right: 0;
      padding-right: 0;
      
   }
}