*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.App{
  display: grid;
  place-items: center;
  height: 100vh;
}
.App_body{
  background-color:  whitesmoke;
  height: 90vh;
  width: 90vw;
  display: flex;
  box-shadow:3px 3px 20px -5px #25d366;
  border-radius: 5px 5px 5px 5px;
  min-width: 435px;
}
@media (max-width:730px){
  .App_body{
     background-color:  whitesmoke;
     height: 100vh;
     width: 100vw;
  }
}
