.Login{
   background-color: #f8f8f8;
   width: 100vw;
   height: 100vh;
   display: grid;
   place-items:center;
}
.loginContainer{
   padding: 100px;
   text-align: center;
   background-color: white;
   border-radius: 10px;
   box-shadow: 5px 5px 5px rgba(0,0,0,0.12);
}
.loginContainer >img {
   object-fit: contain;
   height: 100px;
   margin-bottom: 36px;
}
.loginContainer >button {
   margin-top: 50px;
   text-transform: inherit !important;
   background-color: green !important;
   color: white;
}