.Chat{
   flex: 0.65;
   display: flex;
   flex-direction: column;
   background-color: whitesmoke;
}
.Chat_header{
   padding: 10px;
   display: flex;
   align-items: center;
   border-left:1px solid rgb(226, 218, 218) ;
}
.header_info{
   flex:1;
   padding-left: 20px;
}
.header_info >h3{
   margin-bottom: 3px;
   font-weight: 500;
}
.header_info >p{
   color: grey;
}
.header_icons{
   display: flex;
   justify-content: space-between;
   min-width: 80px;
}
.Chat_body{
   background-image: url("https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png");
   flex: 1;
   background-repeat: repeat;
   background-position: center;
   padding: 30px;
   overflow: scroll;
   overflow-x: hidden;
}

.messege{
   position: relative;
   font-size: 16px;
   padding: 8px;
   background-color: white;
   border-radius: 10px;
   width: fit-content;
   margin-bottom: 30px;
}
.chat_reciever{
   margin-left: auto;
   background-color: greenyellow;
}
.chat_name{
   position: absolute;
   top: -15px;
   font-weight: 800;
   font-size: xx-small;
}
.timestamp{
   margin-left: 10px;
   font-size: xx-small;
}
.Chat_info{
   display: flex;
   justify-content: space-between;
   align-items: center;
   height: 60px;
   border-top: 1px solid lightgray;
}
.Chat_info > form{
   flex:1;
   display: flex;
}
.Chat_info >form > input{
   flex: 1;
   border-radius: 30px;
   padding: 10px;
   outline: none;
   border: none;
}
.Chat_info >form >button{
   display: none;
}
.Chat_info > .MuiSvgIcon-root{
   color: grey;
}
 @media (max-width:700px){
   .header_icons{
      display: flex;
      flex-direction: column;
      justify-content: space-between;
   } 
} 
@media (max-width:500px){
   .Chat{
      
      display: flex;
      flex-direction: column;
      background-color: whitesmoke;
   }
}
